@import url(https://fonts.googleapis.com/css?family=Roboto:500);
.App {
  text-align: center;
  padding: 2% 5%;
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
  margin-bottom: 20px;
}

.city {
  width: 250px;
}

#city-search {
  display: flex;
  flex-direction: column;
  margin: auto;
  position: relative;
}

.suggestions {
  width: 250px;
  position: absolute;
  top: 45px;
  z-index: 2;
  align-self: center;
  margin: 0;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}

#root {
  background-color: pink;
}

.details-btn {
  position: absolute;
  right: 10px;
  bottom: 10px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  background-color: #ffcc80;
  border: 1px solid black;
}

.showDetailsButton {
  position: absolute;
  right: 10px;
  bottom: 10px;
  padding: 11px 15px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  background-color: pink;
  font-size: large;

}

.alerts-container {
  position: fixed;
  top: 0px;
  left: 20px;
  width: 250px;
  z-index: 1000;
}

.charts-container {
  display: flex;
  justify-content: space-around; 
  flex-wrap: wrap; 
  gap: 10px; 
}

.charts-container > div {
  flex: 1; 
  min-width: 300px; 
  max-width: 600px;
  margin-bottom: 20px;
  margin-top: 20px;
  border: 2px solid white; 
  padding: 10px; 
  background-color: white; 
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

h1 {
  font-family: 'Rammetto One', sans-serif;
  color: white;
  
}



.event {
  background-color: white;
  display: inline-block;
  padding: 20px 20px 50px;
  margin: 10px 0;
  text-align: left;
  border-radius: 4px;
  width: 100%;
  max-width: 1000px;
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(0, 0, 0, 0.1);
}

